
.Dossier
{
    margin-right: 30px;
    margin-bottom: 40px;

    height: 300px;

    box-shadow: 1px 1px 15px grey;

    .deplacer
    {
        position: relative;
        bottom : 165px;
        left: 10px;

        padding: 8px 5px;

        background-color: gray;
        border-radius: 20px;
        opacity: 70%;

        .sortIcon
        {
            margin: 2px 1px 0px 0px;
        }
    }

    .deplacer:hover
    {
        cursor: pointer;
    }

    img
    {
        margin-left: -35px;
    }

    .info
    {
        margin-top: -5px;
        height: 40%;

        text-align: left;

        padding-top: 20px;
        padding-left: 15px;
        box-sizing: border-box;
        display: flex;
        flex-direction: column;

        h2
        {
            margin-bottom: 15px;
        }

        .boutonVertical
        {
            align-self: flex-end;
        }
        .boutonVertical:hover
        {
            cursor: pointer;
        }
    }

    
}